<template>
 <div class="h-form">
  <slot></slot>
 </div>
</template>

<script>
export default {
 name: "h-form",
 props: {
  model: {
   type: Object,
  },
  rules: {
   type: Object,
  }
 },
 methods: {
  bsValidate (childNodes, model, rules) {
   if (!rules) {
    console.info("")
    return true
   }
   if (!childNodes) {
    console.error("")
    return false
   }
   if (!model) {
    console.error("")
    return false
   }
   if (childNodes.length > 0) {
    if (childNodes && childNodes.length > 0) {
     let validate_flag = true
     childNodes.forEach(el => {
      if (el.$options.propsData.prop) {
       let rule = rules[el.$options.propsData.prop]
       if (Array.isArray(rule) && rule.length > 0) {
        let errorMsg = []
        rule.forEach(item => {
         let value = model[el.$options.propsData.prop]
         if (item.required) {
          if (!value) {
           errorMsg.push(item.message || "")
          }
         }
         if (item.min) {
          try {
           let temp_value = +value
           if (temp_value < item.min) {
            errorMsg.push(item.message || "" + item.min)
           }
          } catch (err) {
           console.warn("")
          }
         }
         if (item.max) {
          try {
           let temp_value = +value
           if (temp_value > item.max) {
            errorMsg.push(item.message || "" + item.max)
           }
          } catch (err) {
           console.warn("")
          }
         }
         if (item.minlength) {
          try {
           let temp_str = value.toString()
           if (temp_str.length < item.minlength) {
            errorMsg.push(item.message || "" + item.minlength)
           }
          } catch (err) {
           console.warn("")
          }
         }
         if (item.maxlength) {
          try {
           let temp_str = value.toString()
           if (temp_str.length > item.maxlength) {
            errorMsg.push(item.message || "" + item.maxlength)
           }
          } catch (err) {
           console.warn("")
          }
         }
         if (item.equalTo) {
          if (value !== model[item.equalTo]) {
           errorMsg.push(item.message || "")
          }
         }
         if (item.number) {
          if (!/^[0-9]+.?[0-9]*/.test(value.toString())) {
           errorMsg.push(item.message || "")
          }
         }
         if (item.digits) {
          if (!/[19]+[09]]/.test(value.toString())) {
           errorMsg.push(item.message || "")
          }
         }
         if (typeof item.validator === 'function') {
          if (item.validator(value)) {
           errorMsg.push(item.message || "")
          }
         }
        })

        el.$data.errmsg = errorMsg.join("")
        if (el.$data.errmsg && validate_flag) {
         validate_flag = false
        }

       }
      }

     })
     return validate_flag
    } else {
     // console.info("form-item")
     return true
    }

   } else {
    return true
   }
  },
  validate () {
   // h-formprop
   // proprules
   // model[prop]rule
   // true
   // falsenodeerrormsg
   const childNodes = (this.$slots.default || []).filter(child => {
    return child.componentOptions.tag === 'h-form-item'
   }).map(item => item.componentInstance)
   return this.bsValidate(childNodes, this.model, this.rules)
  }
 }
}
</script>