<template>
  <div class="border-card primary bigger shadow h-filemanager" @contextmenu="containermenu($event)"
    @mousedown="containermousedown">
    <div class="border-card-header">
      <div class="align-center">
        <h-breadcrumb separator="/" :list="pidpaths" @bread-click="clickPath" @bread-back="pidback">
        </h-breadcrumb>
      </div>

      <div>
        <h-search v-model="dataForm.searchText" @search="searchSubmit"></h-search>
        <h-button type="primary" size="small" @click="upload"></h-button>
        <h-button type="primary" size="small" @click="addDir"></h-button>
        <h-button v-if="show_paste && pasteHereFunc" size="small" type="primary" @click="pasteHere"></h-button>
        <h-button type="primary" size="small" @click="getImageList()"></h-button>
        <!-- <h-button type="primary" size="small"></h-button> -->
        <!-- <h-button type="primary"  size="small" ></h-button> -->
        <!-- <h-button type="danger"  size="small"  @click="shear()"></h-button>
      <h-button type="danger"  size="small"  @click="delItems()"></h-button> -->
        <!-- <h-button type="danger"  size="small"  @click="flatAll()"></h-button> -->
      </div>
    </div>

    <div ref="imageContainer" class="image-container">
      <div v-for="(item, index) in list" :key="index" :id="'image_item_' + item.id" class="image-item" :class="{
          dir: item[dirFlagKey],
          selected: selectList.findIndex((item1) => item1.id === item.id) > -1,
        }" @dblclick.stop="clickFoldItem(item)" @contextmenu="contextmenu($event, index)"
        @mousedown="mouseDown($event, index)">
        <!-- <div v-if="item.dir" class="image">
          <i class="icon-folder"></i>
        </div> -->
        <div class="image">
          <img v-if="item[dirFlagKey]" class="image-dir" src="../../assets/.png" alt="" />
          <img v-else class="image-image" :src="item.url" alt="" />
        </div>

        <div class="image-name">
          <!-- <input v-if="item.rename_active" v-model="item.name" @keyup.enter="item.rename_active=undefined;updateItem(item);"> -->
          <span>{{ item.name }}</span>
        </div>
      </div>
    </div>
    <!-- {{ dataForm.page }} / {{ dataForm.pagesize }} -->


    <div class="border-card-footer">
      <div v-for="(item, index) in pidpaths" :key="index" style="display: flex; justify-content: center;">
        <h-pagination v-if="index === pidpaths.length - 1" :page="item.page" :pagesize="dataForm.pagesize"
          :page-sizes="[10, 20, 30]" :total="listTotal" @change-page="changePage"
          @change-pagesize="handleSizeChange"></h-pagination>
      </div>
    </div>



    <h-dialog title="" :visible.sync="dirShow" append-to-body>
      <h-form ref="dirFormRef" :model="dirForm" :rules="nameRules">
        <h-form-item :prop="dirNameKey" label="">
          <h-input v-model="dirForm[dirNameKey]" placeholder="" />
        </h-form-item>
      </h-form>
      <template slot="footer">
        <div class="flex justify-end">
          <h-button @click="dirShow = false"></h-button>
          <h-button type="primary" @click="submitDir"></h-button>
        </div>
      </template>
    </h-dialog>

    <h-dialog title="" :visible.sync="renameShow" append-to-body>
      <h-form ref="renameFormRef" :model="renameForm" :rules="nameRules">
        <h-form-item :prop="dirNameKey" label="">
          <h-input v-model="renameForm[dirNameKey]" placeholder="" />
        </h-form-item>
      </h-form>

      <template slot="footer">
        <h-button @click="renameShow = false"></h-button>
        <h-button type="primary" @click="submitRename"></h-button>
      </template>
    </h-dialog>
  </div>
</template>

<script>
import * as utils from "../../utils";
import keydown_mixin from "../../utils/keydown.js"
import axios from "../../utils/http";
export default {
  name: "h-filemanager",
  mixins: [keydown_mixin],
  data () {
    return {
      dirFormName: "",
      dataForm: {
        page: 0,
        pagesize: 10,
        parentid: "", // id
        searchText: "", // pid
      },
      pidpaths: [
        {
          name: "",
          parentid: "",
          page: 0,
          pagesize: 10,
        },
      ],
      list: [],
      listTotal: 0,

      dirForm: {

      },
      dirShow: false,
      renameForm: {
      },
      renameShow: false,

      nameRules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
        ],
      },

      selectList: [],
      shearList: [], // 
      copyList: [], // 

      originPath: {
        x: 0,
        y: 0,
        fold_index: 0,
        click_type: "",
      },

      // imageItemHeight: 200,
    };
  },
  props: {
    multiple: { // 
      type: Boolean,
      default: true,
    },
    size: {
      type: Number, // 
    },

    uploadUrl: {
      type: String,
    },
    getListUrl: {
      type: String,
    },
    addDirUrl: {
      type: String,
    },
    putBatchUrl: {
      type: String,
    },
    deleteBatchUrl: {
      type: String,
    },
    putUrl: {
      type: String,
    },

    fileKey: {
      type: String,
      default: 'file'
    },
    parentidKey: {
      type: String,
      default: 'parentid'
    },
    searchKey: {
      type: String,
      default: 'searchText'
    },
    pageKey: {
      type: String,
      default: "page"
    },
    pagesizeKey: {
      type: String,
      default: "pagesize"
    },
    dirNameKey: {
      type: String,
      default: "name"
    },

    // dir
    dirFlagKey: {
      type: String,
      default: "dir"
    },
    uploadFunc: Function, // 
    getImageListFunc: Function, // 
    submitDirFunc: Function, // 
    delItemsFunc: Function, // 
    moveFunc: Function, // 

    // 
    submitRenameFunc: Function, // 
    pasteHereFunc: Function, // 
    disband_foldFunc: Function, // 
  },
  computed: {
    show_paste () {
      // 
      return Array.isArray(this.shearList) && this.shearList.length > 0;
    },
  },
  mounted () {
    this.refreshImageList();
    this.dirForm[this.dirNameKey] = ""
    this.dirForm[this.parentidKey] = ""
    this.renameForm[this.dirNameKey] = ""

    this.nameRules = {
      [this.dirNameKey]: [
        { required: true, message: '', trigger: 'blur' },
      ],
    }
  },
  methods: {
    // set_imageItemHeight() {
    //   const imageItem = document.querySelector(".h-filemanager .image-container .image-item")
    //   console.dir(imageItem)
    //   this.imageItemHeight = imageItem.offsetWidth
    // },

    /**
     * 
     */
    upload () {
      utils.chooseFiles(
        {
          accept: "image/*",
          multiple: this.multiple,
        },
        async (tempFiles) => {

          const loading = this.$loading({
            lock: true,
            text: "",
            spinner: "icon-loading",
            background: "rgba(0, 0, 0, 0.2)",
          });


          const reslist = []
          // todo //  // shift
          for (let i = 0; i < tempFiles.length; i++) {
            const formData = new FormData();
            formData.append(this.fileKey, tempFiles[i]);
            formData.append(
              this.parentidKey,
              this.pidpaths[this.pidpaths.length - 1].parentid
            );
            if (this.uploadFunc) {
              try {
                const res = await this.uploadFunc(formData)
                reslist.push(res)
              } catch (err) {
                console.error(err)
                reslist.push(null)
              }
            } else {
              try {
                const res = await axios.post(this.uploadUrl, formData, {
                  hideloading: true,
                });
                reslist.push(res)
              } catch (err) {
                console.error(err)
                reslist.push(null)
              }
            }
          }

          this.$emit("after-upload", reslist)
          this.getImageList();
          loading.close();
        }
      );


    },
    /**
     * 
     */
    async refreshImageList () {
      if (this.dataForm.page === 1 && this.dataForm.pagesize === 10) {
        this.getImageList();
      } else {
        this.pidpaths[this.pidpaths.length - 1].page = 1;
        this.pidpaths[this.pidpaths.length - 1].pagesize = 10
        this.dataForm.page = 1
        this.dataForm.pagesize = 10
        this.getImageList();
      }
    },
    /**
     * 
     * @param {number} p
     */
    changePage (p) {
      this.dataForm.page = p
      this.pidpaths[this.pidpaths.length - 1].page = p
      this.getImageList()
    },
    /**
     * 
     */
    handleSizeChange (ps) {
      this.dataForm.page = 1
      this.pidpaths[this.pidpaths.length - 1].page = 1
      this.dataForm.pagesize = ps
      this.pidpaths[this.pidpaths.length - 1].pagesize = ps
      this.getImageList()
    },
    /**
     * /
     */
    async getImageList () {
      if (this.getImageListFunc) {
        const params = {
          [this.pageKey]: this.dataForm.page,
          [this.pagesizeKey]: this.dataForm.pagesize,
          [this.parentidKey]: this.pidpaths[this.pidpaths.length - 1].parentid,
          [this.searchKey]: this.dataForm.searchText
        }
        try {
          const { list, total } = await this.getImageListFunc(params)
          this.list = list;
          this.listTotal = total
          this.selectList = [];
        } catch (err) {
          console.error(err)
        }
      } else {
        try {
          const res = await axios.get(
            `${this.getListUrl}?${this.pageKey}=${this.dataForm.page}&${this.pagesizeKey}=${this.dataForm.pagesize
            }&${this.parentidKey}=${this.pidpaths[this.pidpaths.length - 1].parentid}&${this.searchKey}=${this.dataForm.searchText}`
          );
          if (res.code === 0) {
            this.list = res.data.list;
            this.listTotal = res.data.total;
            this.selectList = [];
          }
        } catch (err) {
          console.error(err)
        }
      }

    },
    /**
     * /
     */
    searchSubmit () {
      this.getImageList()
    },
    /**
     * 
     */
    addDir () {
      this.dirShow = true;
    },
    /**
     * 
     */
    async submitDir () {
      if (this.$refs.dirFormRef && this.$refs.dirFormRef.validate()) {
        if (this.submitDirFunc) {
          try {
            const res = await this.submitDirFunc({
              ...this.dirForm,
              [this.parentidKey]: this.pidpaths[this.pidpaths.length - 1].parentid,
            })
            this.$emit("after-add-dir", res)
            this.getImageList();
            this.dirShow = false;
          } catch (err) {
            console.error(err)
          }
        } else {
          try {
            const res = await axios.post(this.addDirUrl, {
              ...this.dirForm,
              [this.parentidKey]: this.pidpaths[this.pidpaths.length - 1].parentid,
            });
            if (res.code === 0) {
              this.getImageList();
              this.dirShow = false;
            }
          } catch (err) {
            console.error(err)
          }
        }


      }

    },
    /**
     * rename
     */
    async submitRename () {
      if (this.$refs.dirFormRef && this.$refs.dirFormRef.validate()) {
        const renameList = this.selectList.map(item => ({
          ...item,
          [this.dirNameKey]: this.renameForm[this.dirNameKey]
        }))
        if (this.submitRenameFunc) {
          try {
            const res = await this.submitRenameFunc(renameList)
            this.$emit("after-rename", res)
            this.renameShow = false
            this.getImageList();
          } catch (err) {
            console.error(err)
          }
        } else {
          try {
            const res = await axios.put(this.putBatchUrl, renameList)
            if (res.code === 0) {
              this.renameShow = false
              this.getImageList();
            }
          } catch (err) {
            console.error(err)
          }
        }
      }
    },
    /**
     * /
     * @param {*} item
     */
    async updateItem (item) {
      const res = await axios.put(this.putUrl, item);
      if (res.code === 0) {
        this.getImageList();
      }
    },
    /**
     * from
     */
    async pasteHere (ev) {
      if (ev) {
        ev.stopPropagation()
        ev.preventDefault()
      }
      if (!Array.isArray(this.shearList) || this.shearList.length === 0) {
        this.$message.error("")
        return
      }
      const parentid = this.pidpaths[this.pidpaths.length - 1].parentid || "";

      try {
        if (this.pasteHereFunc) {
          const res = await this.pasteHereFunc(this.shearList && this.shearList.map(item => ({
            ...item,
            [this.parentidKey]: parentid,
          })))
        } else {
          const res = await axios.put(this.putBatchUrl, this.shearList && this.shearList.map(item => ({
            ...item,
            [this.parentidKey]: parentid,
          })));
        }
        this.shearList = [];
        this.getImageList();

      } catch (err) {
        console.error(err)
      }


    },
    async disband_fold (id, targetid) {
      try {
        if (this.disband_foldFunc) {
          const res = await this.disband_foldFunc({
            id, targetid,
          })
          this.$message.success("");
          this.getImageList();
        } else {
          const res = await axios.delete(
            "/api/disband?id=" + id + "&targetid=" + targetid
          );
          if (res.code === 0) {
            this.$message.success("");
            this.getImageList();
          }
        }
      } catch (err) {
        console.error(err)
      }
    },
    /**
     * /
     */
    async delItems (id) {
      let ids = "";
      if (id) {
        // 
        ids = id;
      } else {
        if (!Array.isArray(this.selectList) || this.selectList.length === 0) {
          this.$message.error("/");
          return;
        }
        ids = this.selectList.map((item) => item.id).join(",");
      }

      try {
        if (this.delItemsFunc) {
          const flag = await this.delItemsFunc({ ids })
          if (flag) {
            this.$message.success("");
            this.getImageList();
          }
        } else {
          const res = await axios.delete(`${this.deleteBatchUrl}?ids=${ids}`);
          if (res.code === 0) {
            this.$message.success("");
            this.getImageList();
          }
        }
      } catch (err) {
        console.error(err)
      }

    },
    /**
     * flatAll
     */
    async flatAll () {
      const res = await axios.post(`/api/flatall`);
      if (res.code === 0) {
        this.$message.success("");
        this.getImageList();
      }
    },
    /**
     * /
     * @param {*} item
     */
    clickFoldItem (item) {
      if (item[this.dirFlagKey]) {
        this.pidpaths.push({
          name: item.name,
          parentid: item.id,
          page: 1,
          pagesize: 10,
        });
        this.refreshImageList();
      }
    },
    /**
     * /
     * @param {*} item
     */
    selectItem (click_index, clicktype) {
      const item = this.list[click_index]
      const findIndex = this.selectList.findIndex((item1) => item1 === item);

      switch (clicktype) {
        case 'ctrl_click':
          if (findIndex > -1) {
            this.selectList.splice(findIndex, 1);

            return false // 
          } else {
            this.selectList.push(item);
          }
          break
        case 'shift_click':
          // eslint-disable-next-line no-case-declarations
          let selectIndexs = []
          this.selectList.forEach((item,) => {
            const fIndex = this.list.findIndex(item1 => item1 === item)
            if (fIndex > -1) {
              selectIndexs.push(fIndex)
            }
          })
          selectIndexs.sort()

          // eslint-disable-next-line no-case-declarations
          const min = Math.min(...selectIndexs)
          // eslint-disable-next-line no-case-declarations
          const tempSelectList = []
          if (click_index < min) {
            for (let i = click_index; i < min + 1; i++) {
              tempSelectList.push(this.list[i])
            }
            this.selectList = tempSelectList
          } else if (click_index > min) {
            for (let i = min; i < click_index + 1; i++) {
              tempSelectList.push(this.list[i])
            }
            this.selectList = tempSelectList
          }
          return this.selectList
        case 'click':
          if (findIndex > -1) { // 
            return item
          } else {
            this.selectList = [item]
          }
      }


    },
    /**
     * 
     */
    shear (ev) {
      if (ev) {
        ev.stopPropagation()
        ev.preventDefault()
      }
      if (!Array.isArray(this.selectList) || this.selectList.length === 0) {
        this.$message.error("")
        return
      }
      this.shearList = this.selectList;
      this.$message.success("");
    },
    /**
     * 
     * @param {*} item
     */
    clickPath (index) {
      console.log(index)
      this.pidpaths = this.pidpaths.slice(0, index + 1);
      this.dataForm.page = this.pidpaths[this.pidpaths.length - 1].page
      this.dataForm.pagesize = this.pidpaths[this.pidpaths.length - 1].pagesize
      this.getImageList();
    },
    /**
     * 
     */
    pidback () {
      this.pidpaths = this.pidpaths.slice(0, this.pidpaths.length - 1);
      this.dataForm.page = this.pidpaths[this.pidpaths.length - 1].page
      this.dataForm.pagesize = this.pidpaths[this.pidpaths.length - 1].pagesize
      this.getImageList();
    },
    /**
     * 
     * @param {*} down_ev
     * @param {*} fold_index
     */
    mouseDown (down_ev, fold_index) {
      down_ev.stopPropagation();
      down_ev.preventDefault();

      document.onmousemove = null
      document.onmouseup = null

      this.originPath.x = down_ev.clientX;
      this.originPath.y = down_ev.clientY;

      let up_set_item; // 
      if (down_ev.ctrlKey && down_ev.button === 0) {
        // ctrl + 
        this.selectItem(fold_index, 'ctrl_click');
      } else if (down_ev.shiftKey && down_ev.button === 0) {
        // shift + 
        this.selectItem(fold_index, 'shift_click');
      } else if (down_ev.button === 0) {
        up_set_item = this.selectItem(fold_index, 'click');
      }
      if (down_ev.button === 2) {
        if (!this.selectList.includes(this.list[fold_index])) {
          this.selectList = [this.list[fold_index]]
        }
        // this.contextmenu(down_ev, fold_index)
      }



      if (down_ev.button === 0) { // 
        let moved = false // 
        document.onmousemove = (move_ev) => {
          move_ev.stopPropagation();
          move_ev.preventDefault();


          moved = true
          this.move_deel(move_ev); // 
        };
        document.onmouseup = (up_ev) => {
          // console.log("onmoues up", fold_index, down_ev);
          up_ev.stopPropagation();
          up_ev.preventDefault();

          if (up_set_item && !moved) {
            this.selectList = [up_set_item]
          }

          document.onmousemove = null;
          this.move_end_deel(up_ev, async (dir_index) => {
            // 
            const target_item = this.list[dir_index];

            try {
              let isMoved = false
              if (target_item[this.dirFlagKey]) {
                // 
                if (!Array.isArray(this.selectList) || this.selectList.length === 0) return

                if (this.moveFunc) {
                  isMoved = await this.moveFunc(this.selectList.map(item => ({
                    ...item,
                    [this.parentidKey]: target_item.id,
                  })))


                } else {
                  isMoved = await axios.put(this.putBatchUrl, this.selectList.map(item => ({
                    ...item,
                    [this.parentidKey]: target_item.id,
                  })))
                }

                if (isMoved) {
                  this.getImageList()
                  this.$message.success("");
                }
              }

            } catch (err) {
              console.error(err)
            }

          });
        };
      } else if (down_ev.button === 2) {
        // console.log(down_ev)
      }

    },
    /**
     * 
     * @param {*} move_ev 
     */
    move_deel (move_ev) {
      const { clientX, clientY } = move_ev;
      for (let index = 0; index < this.list.length; index++) {
        if (this.selectList.includes(this.list[index])) {
          const { x, y } = this.originPath;
          const element = document.getElementById("image_item_" + this.list[index].id);
          element.style.transform = `translate(${clientX - x}px, ${clientY - y
            }px)`;
          element.style.zIndex = 123;
        } else {
          if (this.list[index][this.dirFlagKey]) {
            const element = document.getElementById("image_item_" + this.list[index].id);
            const { top, bottom, left, right } =
              element.getBoundingClientRect();
            if (
              left < clientX &&
              right > clientX &&
              top < clientY &&
              bottom > clientY
            ) {
              element.classList.add("active");
            } else {
              element.classList.remove("active");
            }
          }
        }
      }
    },
    /**
     * 
     * @param {*} up_ev
     * @param {*} callback
     */
    move_end_deel (up_ev, callback) {
      const { clientX, clientY } = up_ev;
      let target_index = -1
      for (let index = 0; index < this.list.length; index++) {
        const element = document.getElementById("image_item_" + this.list[index].id);
        if (element) {
          if (this.selectList.includes(this.list[index])) {
            element.style.transform = `translate(0, 0)`;
            element.style.zIndex = 12;
          } else {
            const { top, bottom, left, right } = element.getBoundingClientRect();
            if (
              left < clientX &&
              right > clientX &&
              top < clientY &&
              bottom > clientY
            ) {
              target_index = index
            }

          }
          element.classList.remove("active");
        }

      }

      // // 
      if (typeof callback === "function" && target_index > -1) {
        callback(target_index);
      }
    },
    /**
     * 
     * @param {*} ev
     * @param {*} fold_index
     */
    contextmenu (ev, fold_index,) {
      if (ev) { // 
        ev.stopPropagation();
        ev.preventDefault();
      }

      // console.log(fold_index);

      const fold_item = this.list[fold_index];

      if (!this.selectList.includes(fold_item)) {
        this.selectList = [fold_item]
      }

      const { clientX, clientY } = ev

      if (fold_item[this.dirFlagKey]) {
        this.$createRightMenu(clientX, clientY, [
          {
            icon_class: "icon-folder-opened",
            text: "",
            click: (item) => {
              this.clickFoldItem(fold_item);
              console.log(item)
            },
          },
          {
            icon_class: "icon-scissors",
            text: "",
            custom_style: {
              display: !this.pasteHereFunc ? 'none' : 'block',
            },
            click: (item) => {
              this.shearList = this.selectList
              this.$message.success("");
              console.log(item)
            },
          },
          {
            icon_class: "",
            text: "",
            custom_style: {
              display: !this.submitRenameFunc ? 'none' : 'block',
            },
            click: (item) => {
              this.renameShow = true
              this.renameForm[this.dirNameKey] = fold_item[this.dirNameKey]
            },
          },
          {
            icon_class: "",
            text: "",
            custom_style: {
              display: !this.disband_foldFunc ? 'none' : 'block',
            },
            click: (item) => {
              this.disband_fold(fold_item.id, fold_item[this.parentidKey] || "");
            },
          },
        ]);
      } else {
        this.$createRightMenu(clientX, clientY, [
          {
            icon_class: "icon-download",
            text: "",
            click: (item) => {
              console.log(item);
            },
          },
          {
            icon_class: "icon-scissors",
            text: "",
            custom_style: {
              display: !this.pasteHereFunc ? 'none' : 'block',
            },
            click: (item) => {
              this.shearList = this.selectList
              this.$message.success("");
              console.log(item)
            },
          },
          {
            icon_class: "",
            text: "",
            custom_style: {
              display: !this.submitRenameFunc ? 'none' : 'block',
            },
            click: (item) => {
              this.renameShow = true
              this.renameForm[this.dirNameKey] = fold_item[this.dirNameKey]
              console.log(item)

            },
          },
          {
            icon_class: "icon-delete",
            text: "",
            click: (item) => {
              this.delItems()
              console.log(item)
            },
          },
          {
            icon_class: "icon-view",
            text: "",
            click: (item) => {
              this.previewSelect()
              console.log(item)
            },
          }
        ]);
      }
    },
    /**
     * 
     */
    previewSelect () {
      if (Array.isArray(this.selectList)) {
        const imageList = this.selectList.filter(item => !item[this.dirFlagKey])
        if (imageList.length === 1) {
          // # 
          this.$hevueImgPreview({
            url: imageList[0].url
          })
        } else if (imageList.length > 1) {
          // # 
          this.$hevueImgPreview({
            multiple: true, // 
            nowImgIndex: 0, // 
            imgList: imageList.map(item => item.url), // 
          })
        }
      }
    },
    /**
     * false
     * @param {*} ev
     */
    isClickOnItem (ev) {
      const { clientX, clientY } = ev
      let flag = false
      for (let index = 0; index < this.list.length; index++) {
        const element = document.getElementById("image_item_" + this.list[index].id);
        const { top, bottom, left, right } = element.getBoundingClientRect();
        if (
          left < clientX &&
          right > clientX &&
          top < clientY &&
          bottom > clientY
        ) {
          flag = true
        }
      }
      return flag
    },
    /**
     * 
     * @param {*} ev
     */
    containermenu (ev) {
      ev.stopPropagation();
      ev.preventDefault();

      this.containermousedown(ev)

      let rightMenuItems = [
        {
          icon_class: "icon-upload2",
          text: "",
          click: (item) => {
            this.upload()
            console.log(item)
          },
        },
        {
          icon_class: "icon-folder-add",
          text: "",
          click: (item) => {
            this.addDir()
            console.log(item)
          },
        },
        {
          icon_class: "icon-refresh",
          text: "",
          click: (item) => {
            this.getImageList()
            console.log(item)
          },
        },

      ]
      if (Array.isArray(this.shearList) && this.shearList.length > 0) {
        rightMenuItems.push({
          icon_class: "",
          text: "",
          click: (item) => {
            this.pasteHere()
            console.log(item)
          },
        },)
      }
      this.$createRightMenu(ev.clientX, ev.clientY, rightMenuItems);
    },

    containermousedown (ev) {
      if (!this.isClickOnItem(ev)) {
        this.selectList = []
        document.onmousemove = null
        document.onmouseup = null
        ev.stopPropagation()
        // ev.preventDefault()
      }
    }
  },
};
</script>

