.h-filemanager {}

.image-container {
  padding-top: $grup-width;
  padding-right: -$grup-width;
  overflow: hidden;
  // width: $filemanager-width;
  box-sizing: border-box;
  margin: 0 auto;
  min-height: 375px;

  .image-item {
    // width: calc(20% - #{$grup-width});
    // padding: $grup-width / 2;
    // margin: $grup-width / 2;
    float: left;
    position: relative;
    z-index: 12;
    box-sizing: border-box;
    border-radius: 12px;
    border: 1px solid transparent;
    display: flex;
    flex-direction: column;
    align-items: center;

    &.active {
      .image {
        filter: grayscale(50%);
      }
    }

    &:hover {
      background: rgba(58, 208, 255, 0.2);
    }

    &.selected {
      background: rgba(58, 208, 253, 0.36);
      border: 1px solid rgb(58, 133, 253);

      &::after {
        content: '';
        position: absolute;
        display: block;
        top: 5px;
        left: 5px;
        width: 19px;
        height: 19px;
        background-repeat: no-repeat;
        background-size: 19px 19px;
        background-image: url(data:image/png;base64,);
      }
    }

    .image {
      // width: calc(20% - #{$grup-width});
      margin: 0 auto;
      // width: 160px;
      // height: 160px;
      display: flex;
      justify-content: center;
      align-items: center;
      object-fit: cover;
      // background: #ccc;
      border-radius: 10px;
      // margin-

      &-image {
        width: 80%;
        height: 80%;
        object-fit: cover;
        border-radius: 10px;
      }

      &-dir {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    .image-name {
      width: 100%;
      margin: 0 auto;
      text-align: center;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      padding: 0 7px 5px;
    }
  }

}
</style>
